.banner{
    background-image: url(./assets/banner2.jpg);
    
  }
  .bannerContent{
    height: 400px;
    display: flex;
    flex-direction: column;
    padding-top:25px ;
    justify-content: space-around;
  }
  .tagline{
    display: flex;
    height: 40%;
    flex-direction: column;
    justify-content: center;
    text-align: center;
  }
  .carousel{
    height: 50%;
    display: flex;
    align-items: center;
  }
  .carouselItem{
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
    text-transform: uppercase;
    color: white;

  }
  .row{
    background-color: #16171a;
    cursor: pointer;
    font-family: "Montserrat";
  }
  .row:hover{
    background-color: #131111;
  }
  .container{
    display: flex;
    align-items: center;
  }
  .coinInfo{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 25px;
    border-right: 2px solid grey;
  }
  .heading{
    font-weight: bold;
    font-family: "Montserrat";
    margin-bottom: 20px;
  }
  .marketData{
    align-self:start;
    padding: 25px;
    padding-top: 10px;
    width: 100%;
  }
  @media (max-width:1200px) {
    .marketData{
      display: flex;
      align-items: start;
      justify-content: space-around;
    }
    
  }
  @media (max-width:800px) {
    .marketData{
      display: flex;
   flex-direction: column;
  align-items:  center;
    }
    
  }
  @media (max-width:600px) {
    .marketData{
    align-items: start;
    }
    
  }
  
